import React from 'react'
import {useEffect } from 'react';
import {Outlet, useLocation } from "react-router-dom";


export const ChContent = (props) => {
    let location = useLocation();
    let pathName = location.pathname;
    pathName.trim();
    const myArray = pathName.split("/");
    console.log(myArray[myArray.length-1]);
    const myArray2 = myArray[myArray.length-1].split("chapter");
    console.log(myArray2[myArray2.length-1]);
    let myString = "0000"+ myArray2[myArray2.length-1] + ".json";
    let chapterID = myString.substr(-10);
    
    return (
        <div>
            {console.log("chapter content request sent")}
             <div id="chContent" className="container"></div>
             {chapterID}
                {useEffect(() => {
                   props.fetchChapter(chapterID);
                }, [])}
                
        </div>
        
    )
}
