import React from 'react'

export default function Footer() {
    let footerStyle = {
        width: "100%",
        position: "relative",
        bottom: "0"

    }
    return (
        <>
            <footer className="bg-dark text-light my-3" style={footerStyle}>
                <p className="text-center"> &copy; Footer</p>
            </footer>
        </>
    )
}
