// import { render } from "react-dom";
import {
  BrowserRouter,
  Routes,
  Route,
  useRoutes
} from "react-router-dom";

import './App.css';
import React, { useState, useEffect } from 'react';
import Header from "./myComponents/Header";
import Todos from "./myComponents/Todos";
import Footer from "./myComponents/Footer";
import { AddTodo } from "./myComponents/AddTodo";
import { About } from "./myComponents/About";
import { ChContent } from "./myComponents/ChContent";


function App() {
  let initTodo;
  if (localStorage.getItem("todos") === null) {
    initTodo = [];
  }
  else {
    initTodo = JSON.parse(localStorage.getItem("todos"))
  }
  const onDelete = (todo) => {
    console.log("i am on delete", todo);
    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
    localStorage.setItem("todos", JSON.stringify(todos));
  }

  async function fetchChapter(chapterID) {
    console.log(chapterID);
    const response = await fetch(`./src2/chapter/${chapterID}`, {
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    })
    console.log(response);
    const data = await response.json();
    const body1 = data.body;
    console.log(data);
    const jsonString = await JSON.stringify(body1);
    console.log(jsonString);
    document.getElementById("chContent").innerHTML = jsonString;
  }

  // fetchChapter('00001.json');
  const addTodo = (title, desc) => {
    console.log("i am adding", title, desc);
    let sno;
    if (todos.length === 0) {
      sno = 1;
    }
    else {
      sno = todos[todos.length - 1].sno + 1;
    }
    const myTodo = {
      sno: sno,
      title: title,
      desc: desc
    }
    setTodos([...todos, myTodo])
    console.log(myTodo)
  }

  const [todos, setTodos] = useState(initTodo);
  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos]);

  // const routes = [
  //   { path : "/", 
  //   element: <> "<div className="container">This is web page home</div>
  //         <AddTodo addTodo={addTodo} />
  //         <Todos todos={todos} onDelete={onDelete} />"
  //         </>
  //    },
  //    {path: "/about", element: <About />},
  //    {path: "about/:chapterID", element: <ChContent fetchChapter={fetchChapter}/>}
  //  ]
  //  let element = useRoutes(routes);
  return (
    <>
      <BrowserRouter>
        <Header title="my-TODO List" searchBar={false} />
        <Routes>
          <Route path="/" element={
            <> <div className="container">This is web page home</div>
              <AddTodo addTodo={addTodo} />
              <Todos todos={todos} onDelete={onDelete} /></>}>
          </Route>
          <Route>
            <Route path="/about" element={<About />}></Route>
            <Route path="about/:chapterID" element={<ChContent fetchChapter={fetchChapter} />}>
              
          </Route>
        </Route>
        <Route path="*" element={
                <main style={{ padding: "1rem" }}>
                  <p>There's nothing here!</p>
                </main>}
              >
            </Route>

      </Routes>
      <Footer />

    </BrowserRouter>
    </>
  );
}



export default App;
